@import "../../../common.module.scss";

.Header {
  background-image: linear-gradient(to bottom, #00000000, #00000040, #000000b0),
    url("../../../img/header.jpg");

  background-size: cover;
  background-position: center;
  height: 100vh;
  width: 100vw;
  display: flex;

  align-items: center;
  justify-content: center;

  div {
    color: $white;
    text-align: center;
    h1,
    h5,
    h6 {
      font-weight: lighter;
    }
  }

  .downwardArrow {
    color: $white;
    position: absolute;
    bottom: 25px;
  }
}

.displayPicture {
  height: 200px;
  width: 200px;
  border-radius: 50%;
  background-image: url("../../../img/me.jpg");
  background-position: center;
  background-size: cover;
  margin: auto;
}
